___CSS_LOADER_EXPORT___.push([module.id, `/* Основные отступы для секции сравнения */
.comparison-section {
  padding: 60px 0;
}

/* Стиль заголовка секции: выравнивание, размеры и цвет */
.comparison-section h1 {
  text-align: center;
  margin-bottom: 40px;
  font-size: 36px;
  color: #1e2a38;
  font-weight: 700;
  letter-spacing: -0.02em;
}

/* Контейнер для селекторов стран: выравнивание и отступ */
.countries {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 40px;
}

/* Общие стили блока выбора страны */
.country-select {
  background: #fff;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

/* Стиль метки в блоке выбора страны */
.country-select label {
  display: block;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: 16px;
}

/* Стиль выпадающего списка */
.country-select select {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border-radius: 6px;
  border: 1px solid #ccc;
  background-color: #fafafa;
  transition: border 0.2s ease-in-out;
}

/* Стили фокуса для выпадающего списка */
.country-select select:focus {
  border-color: #1e88e5;
  box-shadow: 0 0 8px rgba(30, 136, 229, 0.3);
}

/* Стиль таблицы сравнения: размеры, фон и тень */
.comparison-table {
  width: 65%;
  border-collapse: collapse;
  background: #fff;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
  margin-top: 20px;
  table-layout: fixed;
  word-wrap: break-word;
  transition: opacity 0.3s ease;
}

/* Общие стили для ячеек таблицы */
.comparison-table th,
.comparison-table td {
  width: 33.33%;
  padding: 18px 16px;
  text-align: center;
  font-size: 15px;
  border-bottom: 1px solid #e6eaf0;
}

/* Стиль текста в ячейках */
.comparison-table td {
  font-weight: 400;
}

/* Подсветка четных строк */
.comparison-table tr:nth-child(even) {
  background-color: #f9fbfc;
}

/* Стиль заголовков колонок */
.comparison-table th {
  background-color: #edf2f7;
  color: #1e2a38;
  font-size: 16px;
}

/* Выделение первой колонки */
.comparison-table td:first-child {
  font-weight: bold;
  text-align: left;
  background-color: #f7f9fa;
}

/* Блок с флагом: выравнивание и отступ */
.flag-select {
  display: flex;
  align-items: center;
  gap: 12px;
}

/* Стили изображения флага */
.flag-select img {
  width: 36px;
  height: 24px;
  border-radius: 4px;
  object-fit: cover;
  border: 1px solid #ccc;
}

/* Анимация скрытия/показа таблицы */
.comparison-table.animate {
  opacity: 0.3;
}

/* Обертка для таблицы: центрирование и позиционирование */
.table-wrapper {
  display: flex;
  justify-content: center;
  position: relative;
}

/* Спиннер загрузки: позиционирование и анимация */
.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 40px;
  height: 40px;
  margin: -20px 0 0 -20px;
  border: 4px solid #ccc;
  border-top-color: #1e88e5;
  border-radius: 50%;
  animation: spin 0.7s linear infinite;
  display: none;
  z-index: 10;
}

/* Ключевые кадры для анимации спиннера */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Подсветка лучшего значения */
.highlight-better {
  background-color: #e3fbe3;
  font-weight: bold;
}

/* Стили кнопки экспорта в PDF */
#export-pdf.button {
  padding: 12px 24px;
  background-color: #1e88e5;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

/* Состояние ховера для кнопки экспорта */
#export-pdf.button:hover {
  background-color: #1565c0;
}

/* Режим печати: убираем тени */
.print-mode {
  box-shadow: none;
  overflow: visible;
  background: white;
}

/* Стили бургер-меню */
.mobile-menu-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  z-index: 1000;
}

/* Полоски бургер-меню */
.mobile-menu-toggle span {
  display: block;
  width: 25px;
  height: 3px;
  background-color: #fff;
  margin: 5px 0;
  transition: all 0.3s ease-in-out;
}

/* Преобразования для иконки 'крестика' */
.mobile-menu-toggle span.rotate-45 {
  transform: rotate(45deg) translate(5px, 5px);
}
.mobile-menu-toggle span.opacity-0 {
  opacity: 0;
}
.mobile-menu-toggle span.rotate-negative-45 {
  transform: rotate(-45deg) translate(7px, -6px);
}

/* Ограничение прокрутки при открытом меню */
body.menu-open {
  overflow: hidden;
}
body.menu-open::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

/* Плавное появление подчеркивания для навигации */
.nav a {
  position: relative;
  transition: color 0.3s ease;
}
.nav a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #f7d774;
  transition: width 0.3s ease;
}
.nav a:hover::after,
.nav a.active::after {
  width: 100%;
}

/* Стиль кнопки Aviasales с иконкой самолета */
.aviasales-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  background: #007aff;
  color: #fff !important;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  padding: 16px 32px;
  box-shadow: 0 2px 8px rgba(0, 122, 255, 0.1);
  cursor: pointer;
  text-decoration: none;
  transition: background 0.2s, box-shadow 0.2s, transform 0.1s;
}
.aviasales-btn:hover,
.aviasales-btn:focus {
  background: #005ecb;
  box-shadow: 0 4px 16px rgba(0, 122, 255, 0.18);
  transform: translateY(-2px) scale(1.03);
  text-decoration: none;
}
.aviasales-btn .plane {
  display: inline-block;
  width: 22px;
  height: 22px;
  margin-right: 4px;
  vertical-align: middle;
}
