___CSS_LOADER_EXPORT___.push([module.id, `/* Мобильное меню */
.mobile-menu-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  padding: 10px;
  z-index: 1000;
}

.mobile-menu-toggle span {
  display: block;
  width: 25px;
  height: 3px;
  background-color: #333;
  margin: 5px 0;
  transition: 0.3s;
}

/* Адаптивные стили */
@media (max-width: 768px) {
  .mobile-menu-toggle {
    display: block;
  }

  .header-content {
    justify-content: space-between;
    padding: 15px;
  }

  .nav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 60vw !important;
    max-width: 200px !important;
    height: 100vh;
    background: #fff;
    padding: 80px 20px 20px !important;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    transition: 0.3s;
    display: flex;
    flex-direction: column;
  }

  .nav.active {
    right: 0;
  }

  .nav a {
    font-size: 18px !important;
    padding: 15px 0 !important;
  }

  .countries {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  .comparison-table {
    font-size: 14px;
  }

  .comparison-table th,
  .comparison-table td {
    padding: 12px 10px;
  }

  .country-select {
    width: 100%;
    max-width: 400px;
  }

  /* Улучшенные стили для мобильных устройств */
  .button {
    width: 100%;
    max-width: 300px;
    padding: 15px;
    font-size: 16px;
  }

  h1 {
    font-size: 24px;
    margin: 20px 0;
  }

  html,
  body {
    max-width: 100vw;
    overflow-x: hidden;
  }
  .container {
    max-width: 100vw;
    overflow-x: hidden;
    box-sizing: border-box;
  }
  .comparison-section {
    max-width: 100vw;
    overflow-x: hidden;
  }
  .table-wrapper {
    width: 100vw;
    max-width: 100vw;
    overflow-x: auto;
    box-sizing: border-box;
  }
  .comparison-table {
    min-width: 600px;
    max-width: 100%;
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }
}

@media (max-width: 600px) {
  html,
  body {
    max-width: 100vw;
    overflow-x: hidden;
  }
  .container {
    max-width: 100vw;
    overflow-x: hidden;
    box-sizing: border-box;
  }
  .comparison-section {
    max-width: 100vw;
    overflow-x: hidden;
  }
  .table-wrapper {
    width: 100vw;
    max-width: 100vw;
    overflow-x: auto;
    box-sizing: border-box;
  }
  .comparison-table {
    min-width: 600px;
    max-width: 100%;
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  /* Улучшенные стили для маленьких экранов */
  .site-header {
    padding: 10px 0;
  }

  .logo {
    font-size: 20px;
  }

  /* Адаптивные отступы */
  .comparison-section {
    padding: 20px 0;
  }

  /* Улучшенные стили для форм */
  select {
    height: 45px;
    font-size: 16px;
  }

  label {
    font-size: 14px;
    margin-bottom: 5px;
  }
}

/* Улучшенные стили для планшетов */
@media (min-width: 769px) and (max-width: 1024px) {
  .container {
    padding: 0 30px;
  }

  .comparison-table {
    font-size: 15px;
  }

  .country-select {
    max-width: 300px;
  }
}

/* Анимации для мобильного меню */
@keyframes slideIn {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes slideOut {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(100%);
  }
}

@media (max-width: 900px) {
  .countries-grid {
    grid-template-columns: repeat(2, 1fr) !important;
    gap: 16px !important;
    padding: 24px 0 !important;
  }
  .country-card img {
    width: 100% !important;
    height: 100px !important;
  }
}

@media (max-width: 600px) {
  .countries-grid {
    grid-template-columns: 1fr !important;
    gap: 18px !important;
    padding: 16px 0 !important;
  }
  .country-card {
    padding: 16px 8px !important;
    font-size: 16px !important;
  }
  .country-card img {
    width: 100% !important;
    height: 90px !important;
  }
}

/* Снимаем ограничения по ширине для .container на странице стран */
body.countries-page .container {
  max-width: 1200px !important;
  width: 90%;
  overflow-x: visible !important;
}
