___CSS_LOADER_EXPORT___.push([module.id, `/* Контейнер grid с 4 равными колонками и отступами */
.countries-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 8px;
  padding: 40px 0;
}

/* Стили карточки страны: фон, скругления, тень и анимация */
.country-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 20px;
  text-align: center;
  transition: transform 0.2s ease;
}

/* Анимация при наведении: немного поднимаем карточку */
.country-card:hover {
  transform: translateY(-4px);
}

/* Фиксированные размеры и оформление изображения */
.country-card img {
  width: 160px;
  height: 80px;
  object-fit: cover;
  border-radius: 4px;
  margin-bottom: 10px;
}

/* Заголовок названия страны: отступы и размер шрифта */
.country-card h3 {
  margin: 10px 0 6px;
  font-size: 18px;
}

/* Описание страны: размер шрифта и цвет */
.country-card p {
  font-size: 14px;
  color: #666;
}`, "",{"version":3,"sources":["webpack://./src/css/countries.css"],"names":[],"mappings":"AAAA,qDAAqD;AACrD;EACE,aAAa;EACb,qCAAqC;EACrC,QAAQ;EACR,eAAe;AACjB;;AAEA,4DAA4D;AAC5D;EACE,gBAAgB;EAChB,mBAAmB;EACnB,yCAAyC;EACzC,aAAa;EACb,kBAAkB;EAClB,+BAA+B;AACjC;;AAEA,uDAAuD;AACvD;EACE,2BAA2B;AAC7B;;AAEA,mDAAmD;AACnD;EACE,YAAY;EACZ,YAAY;EACZ,iBAAiB;EACjB,kBAAkB;EAClB,mBAAmB;AACrB;;AAEA,uDAAuD;AACvD;EACE,kBAAkB;EAClB,eAAe;AACjB;;AAEA,0CAA0C;AAC1C;EACE,eAAe;EACf,WAAW;AACb","sourcesContent":["/* Контейнер grid с 4 равными колонками и отступами */\r\n.countries-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(4, 1fr);\r\n  gap: 8px;\r\n  padding: 40px 0;\r\n}\r\n\r\n/* Стили карточки страны: фон, скругления, тень и анимация */\r\n.country-card {\r\n  background: #fff;\r\n  border-radius: 12px;\r\n  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);\r\n  padding: 20px;\r\n  text-align: center;\r\n  transition: transform 0.2s ease;\r\n}\r\n\r\n/* Анимация при наведении: немного поднимаем карточку */\r\n.country-card:hover {\r\n  transform: translateY(-4px);\r\n}\r\n\r\n/* Фиксированные размеры и оформление изображения */\r\n.country-card img {\r\n  width: 160px;\r\n  height: 80px;\r\n  object-fit: cover;\r\n  border-radius: 4px;\r\n  margin-bottom: 10px;\r\n}\r\n\r\n/* Заголовок названия страны: отступы и размер шрифта */\r\n.country-card h3 {\r\n  margin: 10px 0 6px;\r\n  font-size: 18px;\r\n}\r\n\r\n/* Описание страны: размер шрифта и цвет */\r\n.country-card p {\r\n  font-size: 14px;\r\n  color: #666;\r\n}"],"sourceRoot":""}]);
