___CSS_LOADER_EXPORT___.push([module.id, `/* Контейнер для основного контента: центрирование и максимальная ширина */
.container {
  display: flex;
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  flex-direction: column;
  align-items: center;
}

/* Стили шапки сайта: фон, цвет текста и тень */
.site-header {
  background: #1e2a38;
  color: #fff;
  padding: 20px 0;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

/* Контейнер внутри шапки: выравнивание логотипа и навигации */
.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}

/* Логотип: размер и жирность шрифта */
.logo {
  font-size: 24px;
  font-weight: bold;
}

/* Стили ссылок навигации: цвет, отступы и переход */
.nav a {
  color: #fff;
  margin-left: 25px;
  text-decoration: none;
  font-weight: 500;
  transition: 0.2s;
}

/* Эффект при наведении на ссылку навигации */
.nav a:hover {
  color: #f7d774;
  background: linear-gradient(90deg, #f7d774, #f9c94e);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Стили футера: фон, цвет текста и отступ */
.site-footer {
  background: #1e2a38;
  color: #e0e0e0;
  text-align: center;
  padding: 20px 0;
  margin-top: 60px;
  font-size: 14px;
}

/* Базовые стили для страницы "О нас": растянуть на всю высоту */
body.about-page,
html.about-page {
  height: 100%;
  margin: 0;
  padding: 0;
}

/* Основная обёртка страницы "О нас": flex для фиксации футера внизу */
body.about-page .page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Контент главной части страницы "О нас": занимает все доступное пространство */
body.about-page main {
  flex: 1;
}

/* Адаптивные стили для мобильных устройств */
@media (max-width: 768px) {
  /* Шапка: внутренние отступы по бокам */
  .header-content {
    width: 100%;
    padding: 0 15px;
  }

  /* Навигация: off-canvas меню слева */
  .nav {
    position: fixed;
    top: 0;
    right: -100%;
    width: 80%;
    max-width: 300px;
    height: 100vh;
    background: #1e2a38 !important;
    padding: 80px 20px 20px;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    z-index: 2000 !important;
  }

  /* Активное состояние меню: сдвигает меню в видимую область */
  .nav.active {
    right: 0;
  }

  /* Стили ссылок в мобильной навигации */
  .nav a {
    color: #fff !important;
    font-size: 22px !important;
    padding: 20px 0 !important;
    margin: 0 !important;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1) !important;
    width: 100% !important;
    text-align: left !important;
    display: block !important;
    background: none !important;
  }

  /* Убираем линию у последней ссылки */
  .nav a:last-child {
    border-bottom: none !important;
  }

  /* Ховер и активное состояние ссылки в мобильном меню */
  .nav a:hover,
  .nav a:active {
    background: rgba(247, 215, 116, 0.08) !important;
    color: #f7d774 !important;
  }

  /* Кнопка открытия мобильного меню */
  .mobile-menu-toggle {
    display: block;
    z-index: 1001;
  }
}
